@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Radley:ital@0;1&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: inherit;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  line-height: 1;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

picture,
video,
canvas,
svg {
  max-width: 100%;
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

.radley {
  font-family: "Radley", serif;
}

body {
  background-color: #121212;
  color: #e3e3e3;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

.top-menu {
  height: 80px;
  border-bottom: 2px solid #272727;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}
.top-menu .user-menu {
  display: flex;
  position: relative;
  align-items: center;
  gap: 10px;
}
.top-menu .user-menu a {
  font-size: 15px;
}
.top-menu .user-menu a:hover {
  color: #e9ff70;
}
.top-menu .user-menu:hover .user-sub-menu {
  opacity: 1;
  pointer-events: all;
}
.top-menu .user-menu .user-sub-menu {
  position: absolute;
  opacity: 0;
  right: -76px;
  pointer-events: none;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content {
  background-color: #272727;
  border-radius: 20px;
  padding: 10px;
  border: 1px solid #6e6d6d;
  transform: translateX(10px);
  padding: 4px 15px 4px 15px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content a {
  font-size: 13px;
}
.top-menu .search {
  display: flex;
  align-items: center;
}
.top-menu .search input {
  background-color: #272727;
  border: 1px solid #272727;
  height: 40px;
  width: 250px;
  font-size: 14px;
  padding: 0 20px 0 20px;
  border-radius: 20px;
  outline: none;
  color: #fff;
  background-image: url("../assets/icons/search.svg");
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: center right 10px;
}
.top-menu .search input::placeholder {
  color: #fff;
  opacity: 0.5;
}
.top-menu .search input:focus {
  border: 1px solid #e9ff70;
}
.top-menu .nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .nav-logo nav ul {
  display: flex;
  align-items: center;
  gap: 10px;
}
.top-menu .nav-logo nav ul li {
  font-size: 15px;
}
.top-menu .nav-logo nav ul li a {
  color: #e3e3e3;
  text-decoration: none;
}
.top-menu .nav-logo nav ul li a:hover {
  color: #e9ff70;
}